<template>
	<div>MESSAGING {{messaging}}</div>
	<div>
		<MessageBubble v-for="(message,index) in messages" :author="message.author" :y="50+index*30+'px'" :message="message.message"></MessageBubble>
	</div>
	<input v-model="sendMessage" v-on:keyup.enter="send">

</template>
<script>
import {db} from "../firebaseDb.js";
import {doc,collection, addDoc, setDoc, getDoc, updateDoc, deleteDoc, getDocs} from "firebase/firestore"; 
import MessageBubble from "./MessageBubble.vue";

export default{
	data(){
		return{
			messages:[],
			sendMessage:"",
			chatID:"",
			messaging:"",
		}
	},
	mounted(){
		
		this.$nextTick(function () {
            window.setInterval(() => {
                this.update();
            },3000);
		})	
	},
	methods:{
		send(){
			this.messages.push({message:this.sendMessage, author:"ME"});
			getDoc(doc(db, "chats", this.chatID)).then(docSnap => {
				var newMessages=docSnap.data().messages;
				newMessages.push(localStorage.getItem("User")+":"+this.sendMessage);
				updateDoc(doc(db,"chats",this.chatID),{
					messages:newMessages,
				});
			});

			

		},
		update(){
			var user=localStorage.getItem("User");
			getDocs(collection(db,"chats")).then(docSnap => {
				docSnap.forEach((doc) => {
					if(doc.data().first==user || doc.data().second==user){
						if(doc.data().first==user) this.messaging=doc.data().second;
						else this.messaging=doc.data().first;
						this.chatID=doc.id;
						for(var i=0;i<doc.data().messages.length;i++){
							var message=doc.data().messages[i];
							var author=message.split(":")[0];
							if(author==user){
								this.messages.push({message:message.split(":")[1], author:"ME"});
							}else{
								this.messages.push({message:message.split(":")[1], author:"OTHER"});
							}
						}
					}
				});
			});
		}
	},
	components:{
		MessageBubble,
	}
}
</script>